<template>
  <div class="container" id="scroll">
     <Header :leftClick="goBack">
    </Header>
    <product-list :with-option="true" @clickOption="handlePopup" :info="regionId"></product-list>
    <!-- 弹出层 -->
    <van-popup v-model="showPopup" position="left" :lazy-render="false" get-container="#app-warp">
      <Category @back="handlePopup" @clickCateItem="clickCateItem" ref="category" :info="regionId"></Category>
    </van-popup>
  </div>
</template>

<script>
import productList from '../../../components/productList'
import Category from './category'
import Header from '@/components/header'

export default {
  props: {
  },
  components: {
    productList,
    Category,
    Header,
  },
  data() {
    return {
      regionId: this.$route.query.regionId,
      showPopup: false,
    }
  },
  beforeRouteLeave(to, from, next) {
    if (from.meta.keepAlive) {
      // 只针对 keep-alive 的页面记录滚动位置
      let $content = document.querySelector('#scroll'); // 列表的外层容器
      let scrollTop = $content ? $content.scrollTop : 0;
      console.log('scrollTop', scrollTop)
      from.meta.scrollTop = scrollTop;
    }
    next();
  },
  activated() {
    let scrollTop = this.$route.meta.scrollTop;
    if (scrollTop && scrollTop !== 0) {
      // 初次进入页面不执行，返回页面执行
      let $content = document.querySelector('#scroll');
      if ($content) {
        this.$nextTick(() => {
          $content.scrollTop = scrollTop;
        })
      }
    }
  },
  computed: {},
  methods: {
    handlePopup() {
      this.showPopup = !this.showPopup
    },
    clickCateItem(item) {
      const id = item.url.replace(/productCateId=/g, '')
      this.$router.push({ name: 'smartChoiceProvinceThirdCate', params: { cateId: id, title: item.navigationName }, query: { regionId: this.regionId } })
    },
    goBack() {
      this.$keepAliveDestroy()
      this.$store.dispatch('doHistoryBack');
    },
  },
  mounted() {
    this.$refs.category.getCategory() // 在这里先把分类组件的数据请求回来
  },
}
</script>

<style lang="scss" scoped>
::v-deep .van-popup--left{
  left: 50% !important;
  transform: translate3d(-3.8rem,-50%,0) !important;
}

.container {
  position: absolute;
  top:0;
  height: 100%;
  overflow-x: hidden;
  ::v-deep .wrapper-box .product-list {
     margin-top: 1.8rem;
  }
}
</style>
