<template>
  <div class="category">
    <div class="title">
      <span class="left" @click="$emit('back')"></span>
      全部分类
    </div>
    <div class="cate-tree">
      <div class="tree-left">
        <div class="item" v-for="item in cates" :key="item.id" @click="clickCate(item)" :class="item.id === activedCate ? 'actived' : ''">
          {{ item.navigationName | clipText(4) }}
        </div>
      </div>
      <div class="tree-right">
        <lazy-component>
          <div class="wrapper" v-for="item in secondCates" :key="item.id">
            <div class="title">{{ item.navigationName }}</div>
            <div class="content">
              <div
                class="content-item"
                v-for="(_item, _index) in item._childCate"
                :key="_item.id"
                :class="_index > lastIndex(item._childCate.length) ? 'last' : ''"
                @click="$emit('clickCateItem', _item)"
              >
                <img v-lazy="_item.imageUrl" />
                <p>{{ _item.navigationName }}</p>
              </div>
            </div>
          </div>
        </lazy-component>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryList } from '@/http/service/smartChoice.js'
import mixin from '../../../components/mixin'
export default {
  mixins: [mixin],
  props: ['info'],
  data() {
    return {
      cates: [],
      activedCate: '',
      secondCates: [],
    }
  },
  methods: {
    lastIndex(len) {
      let n = len % 3 === 0 ? 3 : len % 3
      return len - 1 - n
    },
    clickCate(item) {
      this.activedCate = item.id
    },
    getCategory(parentId = 0) {
      let params = {
        module: 2, // 智选固定传2
        parentId, // 上级编号，最左侧传0
        regionId: this.info, // 场馆id
      }
      getCategoryList(params).then(res => {
        if (res.success) {
          if (parentId === 0) {
            // 左侧分类
            this.cates = res.data
            this.activedCate = this.cates[0].id // 拿到左侧的数据后立即设置首项为active状态，同时触发右侧请求
          } else {
            // 右侧分类
            let second = [] // 二级分类
            let third = [] // 三级分类
            res.data.forEach(v => {
              // parentId等于一级分类的是二级分类，否则是三级分类
              v.parentId === parentId ? second.push(v) : third.push(v)
            })
            second.forEach(v => {
              third.forEach(val => {
                if (val.parentId === v.id) {
                  v._childCate ? v._childCate.push(val) : (v._childCate = [val])
                }
              })
            })
            this.secondCates = second
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
  },
  watch: {
    activedCate: function(val) {
      this.getCategory(val)
    },
  },
  filters: {},
  created() {
    // this.getCategory()
  },
}
</script>

